import React from 'react';
import {Consumer} from "./context";

const Counter = ({index}) => { //Destructuring...
    
    return (
        <Consumer>{
            ({actions, players}) => {
                const scoreChange = actions.scoreChange;
                return (
                    <div className="counter">
                        <button className="counter-action decrement" onClick={() => scoreChange(index, -1)}> - </button>
                        <span className="counter-score">{players[index].score}</span>
                        <button className="counter-action increment" onClick={() => scoreChange(index, 1)}> + </button>
                    </div>
                );
            }
        }</Consumer>
    );
}

export default Counter;