import React, {Component} from 'react';

const ScoreboardContext = React.createContext();

export class Provider extends Component {
    
    state = {
        players: [
            {
                name: "Steve",
                score: 1,
                id: 1
            },
            {
                name: "Treasure",
                score: 0,
                id: 2
            },
            {
                name: "Ashley",
                score: 0,
                id: 3
            },
            {
                name: "James",
                score: 0,
                id: 4
            }
        ]
    };
    
    prevPlayerId = 4;
    
    handleAddPlayer = (name) => {
        this.setState( prevState => ({
            players: [
                ...prevState.players, //All players already in list
                {
                    name,
                    score: 0,
                    id: this.prevPlayerId += 1
                }
            ]
        }));
    }
    handleRemovePlayer = (id) => {
        this.setState( prevState => ({
            players: prevState.players.filter(p => p.id !== id)
        }));
    }
    
    handleScoreChange = (index, delta) => {
        this.setState( prevState => ({
            score: prevState.players[index].score += delta
        }));
    }
    
    getHighestScore = () => {
        let highestScore = this.state.players.reduce((score, player) => {
            return Math.max(score, player.score)
        }, 0);
        
        if (highestScore) {
            return highestScore;
        }
        return null;
    }
    
    render() {
        return (
            <ScoreboardContext.Provider value={{
                players: this.state.players,
                actions: {
                    scoreChange: this.handleScoreChange,
                    removePlayer: this.handleRemovePlayer,
                    isHighestScore: this.getHighestScore,
                    addPlayer: this.handleAddPlayer
                },
            }}>
                {this.props.children}
            </ScoreboardContext.Provider>
        );
    }
}
export const Consumer = ScoreboardContext.Consumer;