import React, {PureComponent} from 'react';
import {Consumer} from "./context";
import Counter from './Counter';
import IsHighestScore from './IsHighestScore';

class Player extends PureComponent {
    
    render() {
        const {index} = this.props; // Destructuring
        
        return (
            <Consumer>{
                ({actions, players}) => {
                    return (
                        <div className="player">
                        <span className="player-name">
                            <button className="remove-player" onClick={() => actions.removePlayer(players[index].id)}>âœ–</button>
                            <IsHighestScore isHighScore={actions.isHighestScore() === players[index].score}/>
                            {players[index].name}
                        </span>
                            <Counter index={index} />
                        </div>
                    );
                }
            }</Consumer>
        )
    }
}

export default Player;